@font-face {
    font-family: 'infront';
    src:    url('../../fonts/infront.eot?diwyx6');
    src:    url('../../fonts/infront.eot?diwyx6#iefix') format('embedded-opentype'),
        url('../../fonts/infront.ttf?diwyx6') format('truetype'),
        url('../../fonts/infront.woff?diwyx6') format('woff'),
        url('../../fonts/infront.svg?diwyx6#infront') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'infront' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

